import React, { Component } from 'react';
import {Core} from 'vhp-api'
import { MenuSideBar, AppContainer, TextButton, SetCollapsibleTab, GetMenuCollapsed } from 'vhp-component-library';
import { DeviceLog } from './DeviceLog/DeviceLog';

import '../Styles/DeviceLog.css'
import '../Styles/DeviceEditor.css'
import { UserSummary } from './DeviceLog/UserSummary';
import { Users } from '../bin/DeviceLogData';

/**
 * Starter class for an application. Contains a set of containers with a MenuBar allowing you to swap between tabs.
 * MenuBar style can be customized, and containers can be added to the Container to add pages to your app
 * Clone and modify this file whenever you want to create a new app that can sit inside App.js or the dock.
 */
export class AdminApp extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: true,
            Containers:[
                <div>
					<TextButton
						text = "Price Book Refresh"
						ClickFunction = {this.TestFunction}
					/>
				</div>,
				<DeviceLog settings = {this.props.settings}/>,
                <div>Work in progress</div>,
                <div>Work in progress</div>
            ],
            currentTab:0,
			collapsed:""
        }
        
        this.SetTab = SetCollapsibleTab.bind(this)
		this.GetMenuCollapsed = GetMenuCollapsed.bind(this)
		this.TestFunction = this.TestFunction.bind(this)
		this.GetApp = this.GetApp.bind(this)
    }
    
    /**
     * Changes the state of the menu's current tab
     * Each time you define a menu bar in a component, include this function
     * @param {Number} TabID 
     */
    SetTab(TabID){
        this.setState({
            currentTab:TabID
        })
    }

	GetMenuCollapsed(collapsed) {
		console.log(collapsed)
		if (collapsed) {
			this.setState({
				collapsed:"collapsed"
			})
		} else {
			this.setState({
				collapsed:""
			})
		}
	}

	TestFunction() {
		console.log("Click function!")
		//turn off host
		let API = new Core({auth:{user:"VOGCH", pswrd:"vogel123"},sync:false})
		let pack = {
			method:"UPDATEJONASSPB"
		}
		API.Ping().then(
			answr=>{
                console.log("LOGIN:",answr)
			}
		)
		API.SENDrequest({pack:pack,request:"ADMIN",route:"STORE"}).then(
			answr=>{
				console.log("JONAS:",answr)
			}
		)
		//Core (vapi)>config>core>vhp-config.json
	}

	GetApp() {
		if (this.state.currentTab == 1) {
			return <DeviceLog settings = {this.props.settings}/>
		} else {
			return <div>
				<TextButton
					text = "Price Book Refresh"
					ClickFunction = {this.TestFunction}
				/>
			</div>
		}
	}

    render() {
        return(
            <>
                <MenuSideBar 
                    tabs = {["Home","Employees", "Data", "Reports"]}
                    SetTab = {this.SetTab}
                    MenuStyle = {this.state.collapsed}
					images = {[
						"https://www.vhpportal.com/repo/assets/icons/activity.png",
                        "https://www.vhpportal.com/repo/assets/icons/devicelog.png",
                        "https://www.vhpportal.com/repo/assets/icons/calendar.png",
						"https://www.vhpportal.com/repo/assets/icons/print2.png"
                    ]}
					CollapseFunction = {this.GetMenuCollapsed}
					bottomActions = {[
						{
							text:"Dash",
							src:"https://www.vhpportal.com/repo/assets/icons/home.png",
							type:"ImageButton",
							keyName:"print",
							id:"invert-button",
							ClickFunction:this.ToggleBoolean,
							data:"showConfirmGoHome"
						}
					]}
					menuButton = 'https://www.vhpportal.com/repo/assets/icons/right-arrow.png'
					moreActions = {[
						{
							src:"https://www.vhpportal.com/repo/assets/icons/logout.png",
							type:"ImageButton",
							ClickFunction:this.props.LogUserOut
						}
					]}
                />
				<AppContainer id = {this.state.collapsed} containerClass = "side-bar-container">
					{this.GetApp()}
				</AppContainer>
            </>
        );
    }
}