import React, { Component } from 'react';
import { BasicHeader, Card, CardContent, CheckFormFill, InputForm, SetProperty, TextButton, Clone } from 'vhp-component-library';
import { dateformat } from '../../bin/vg-gentools';

let FormTitles = {
	"devID":"Device ID",
	"empID":"Employee ID",
	"type":"Type",
	"name":"Name",
	"manf":"Manufacturer",
	"model":"Model",
	"serial":"Serial Number",
	"cardNum":"Card Number",
	"iccid":"ICC ID",
	"lock":"Lock",
	"purchasePrice":"Purchase Price",
	"purchaseDate":"Purchase Date",
	"upgradeDate":"Upgrade Date",
	"simNum":"Sim Number",
	"simRef":"Sim Ref"
}

/**
 * @todo Load data from props
 */
export class DeviceEditor extends Component {
    constructor(props) {
        super(props)

        this.state = {
            devID:"",
			empID:"",
			type:"",
			name:"",
			manf:"",
			model:"",
			serial:"",
			cardNum:"",
			iccid:"",
			lock:"",
			purchaseDate:"",
			upgradeDate:"",
			simNum:"",
			simRef:"",
			purchasePrice:"",
			formReqs:{
				devID:"",
				empID:"",
				type:""
			}
        }

		this.GetDeviceForm = this.GetDeviceForm.bind(this)
		this.AddDevice = this.AddDevice.bind(this)

		this.SetProperty = SetProperty.bind(this)
		this.CheckFormFill = CheckFormFill.bind(this)
    }

	componentDidMount() {
		if (this.props.device) {
			let state = Clone(this.state)
			for (let key in state) {
				if (this.props.device[key] != undefined) {
					state[key] = this.props.device[key]
				}
			}

			this.setState({
				...state
			})
		}
	}

	/**
	 * @todo consolidate this to only update state in this component, not in device log
	 * @returns 
	 */
	GetDeviceForm() {
		let formdata = []
		if (this.props.device) {
			for (let key in FormTitles) {
				formdata.push(
					{
						title:FormTitles[key] + ":",
						inputType:"TextInput",
						value:this.state[key],
						data:key,
						ChangeFunction:this.SetProperty
					}
				)
				if (key == "upgradeDate" || key == "purchaseDate") {
					formdata[formdata.length - 1].type = "date"
					formdata[formdata.length - 1].value = dateformat(this.state[key])
				}
				if (key == 'simNum' || key == "purchasePrice") {
					formdata[formdata.length - 1].type = "number"
				}
				
			}
		} else {
			for (let key in FormTitles) {
				formdata.push(
					{
						title:FormTitles[key] + ":",
						inputType:"TextInput",
						value:this.state[key],
						data:key,
						ChangeFunction:this.SetProperty
					}
				)
				if (key == "upgradeDate" || key == "purchaseDate") {
					formdata[formdata.length - 1].type = "date"
				}
				if (key == 'simNum' || key == "purchasePrice") {
					formdata[formdata.length - 1].type = "number"
				}
				if (this.state.formReqs[key] != undefined) {
					formdata[formdata.length - 1].formItemClass = this.state.formReqs[key]
				}
			}
		}
		

		return <InputForm 
			formdata = {formdata}
		/>
	}

	AddDevice() {
		this.setState({
			formReqs:this.CheckFormFill(this.state.formReqs, this.state)
		})

		if (this.state.devID !== "" && this.state.empID !== "") {
			this.props.AddDevice(this.state)
			this.props.CloseFunction(this.props.closeData)
		}
	}

    render() {
        return(
            <Card
				id = "device-editor"
				title = "Device Editor"
				titlebar = {true}
				actions = {{minimize:null, close:this.props.CloseFunction}}
				data = {{minimize:null, close:this.props.closeData}}
			>
				{this.props.device&&<div id = "dev-editor-user-info">
					<BasicHeader 
						headerClass = "left-align"
						text = {this.props.device.name + " - " + this.props.user.name}
					/>
					<div className='h4'>Employee #: {this.props.user.empID}</div>
				</div>}
				<CardContent cardContentClass = "device-editor-form">
					{this.GetDeviceForm()}
				</CardContent>
				{!this.props.device&&<TextButton 
					text = "Add Device"
					ClickFunction = {this.AddDevice}
					data = {this.state}
				/>}
				{this.props.device&&<TextButton 
					text = "Update"
					ClickFunction = {this.props.UpdateDevice}
					data = {{...this.state, _id:this.props.device._id}}
				/>}
			</Card>
        );
    }
}