import React, { Component } from 'react';
import { BasicHeader, Card, CardContent, CheckFormFill, InputForm, SetProperty, TextButton, Clone, ToggleBoolean } from 'vhp-component-library';

let FormTitles = {
	empID:"Employee ID",
	name:"Name",
	jobDesc:"Job Description",
	fName:"First Name",
	lName:"Last Name",
	repTo:"Reports To",
	title:"Job Title",
	type:"Job Type",
	dept:"Department"
}

/**
 * @todo Load data from props
 */
export class EmployeeEditor extends Component {
    constructor(props) {
        super(props)

        this.state = {
            empID:"",
			name:"",
			fName:"",
			lName:"",
			repTo:"",
			title:"",
			dept:{text:"Select one", value:null},
			type:{text:"Select one", value:null},
			formReqs:{
				empID:"",
				name:""
			}
        }

		this.GetEmployeeForm = this.GetEmployeeForm.bind(this)
		this.AddEmployee = this.AddEmployee.bind(this)

		this.SetProperty = SetProperty.bind(this)
		this.CheckFormFill = CheckFormFill.bind(this)
		this.ToggleBoolean = ToggleBoolean.bind(this)
		this.AddToDeptList = this.AddToDeptList.bind(this)
    }

	AddToDeptList(opt) {
		console.log(opt)
	}

	/**
	 * @todo consolidate this to only update state in this component, not in device log
	 * @returns 
	 */
	GetEmployeeForm() {
		let formdata = []
		for (let key in FormTitles) {
			formdata.push(
				{
					title:FormTitles[key] + ":",
					inputType:"TextInput",
					value:this.state[key],
					data:key,
					ChangeFunction:this.SetProperty
				}
			)
			if (key == 'dept') {
				formdata[formdata.length - 1].inputType = 'DynamicDropDown'
				formdata[formdata.length - 1].selected = {text:"Select one", value:null}
				formdata[formdata.length - 1].list = [
					{text:"100",value:"100"},
					{text:"200",value:"200"},
					{text:"300",value:"300"},
					{text:"350",value:"350"},
					{text:"400",value:"400"},
					{text:"450",value:"450"},
					{text:"BEE",value:"BEE"},
					{text:"BCE",value:"BCE"}
				]
			}
			if (key == 'type') {
				formdata[formdata.length - 1].inputType = 'DynamicDropDown'
				formdata[formdata.length - 1].selected = {text:"Select one", value:null}
				formdata[formdata.length - 1].list = [
					{text:"TECH",value:"TECH"},
					{text:"CONSULTANT",value:"CONSULTANT"},
					{text:"MANAGER",value:"MANAGER"},
					{text:"DISPATCH",value:"DISPATCH"},
					{text:"OFFICE",value:"OFFICE"},
					{text:"DATAMAN",value:"DATAMAN"},
				]
			}
			if (this.state.formReqs[key] != undefined) {
				formdata[formdata.length - 1].formItemClass = this.state.formReqs[key]
			}
		}
		

		return <InputForm 
			formdata = {formdata}
		/>
	}

	AddEmployee() {
		this.setState({
			formReqs:this.CheckFormFill(this.state.formReqs, this.state)
		})

		if (this.state.name !== "" && this.state.empID !== "") {
			this.props.AddEmployee(this.state)
			this.props.CloseFunction(this.props.closeData)
		}
	}

	UpdateEmployee(data) {

	}

    render() {
        return(
            <Card
				id = "employee-editor"
				title = "Employee Creator"
				titlebar = {true}
				actions = {{minimize:null, close:this.props.CloseFunction}}
				data = {{minimize:null, close:this.props.closeData}}
			>
				<CardContent cardContentClass = "device-editor-form">
					{this.GetEmployeeForm()}
				</CardContent>
				<TextButton 
					text = "Add Employee"
					ClickFunction = {this.AddEmployee}
				/>
			</Card>
        );
    }
}