import React, { Component } from 'react';

import {Image} from 'vhp-component-library'

export class Warning extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
            <div className={'warning ' + this.props.color}>
				<Image src = "https://www.vhpportal.com/repo/assets/icons/info2.png"/>
				<div className='warning-text'>{this.props.message}</div>
			</div>
        );
    }
}