import React, { Component } from 'react';


/**
 * A react version of the HTML image component
 * @param {URL} src | the link to the image.
 * @param {String} id | the id of the component.
 * @param {String} imageClass | the class of the component.
 */
export class Image extends Component {
    constructor(props) {
        super(props)

        if (props.imageClass != undefined) {
            this.imageClass = "image " + props.imageClass
        } else {
            this.imageClass = "image"
        }
    }

    render() {
        return(
            <img src={this.props.src} className = {this.imageClass} id = {this.props.id}/>
        );
    }
}