import React, { Component } from 'react';
import { CardContent } from './CardContent';
import { CardTitlebar } from './CardTitlebar';



/**
 * A basic card class for displaying content. Card will fill it's content with the provided children.
 * @param {Boolean} titlebar | determines whether a card will display a title bar.
 * @param {Object} actions | collection of functions which control actions, in format of {minimize:{x}, close:{y}}
 * @param {Object} data | collection of data associated with each action, in format of {minimize:{x}, close:{y}}
 * @param {String} title | the title displayed in the titlebar
 * @param {String} cardClass | the class associated with the card
 * @param {String} id | the id used with the card, also set as the data-testid. The CardContent child sets its id as *id* + "-card-content".
 */
export class Card extends Component {
    constructor(props) {
        super(props)

        if (props.cardClass) {
            this.cardClass = "card" + ' ' + this.props.cardClass
        } else {
            this.cardClass = 'card'
        }
    }

    render() {
        return(
            <div className={this.cardClass} id={this.props.id} data-testid = {this.props.id}>
				{this.props.titlebar&&<CardTitlebar title={this.props.title} actions = {this.props.actions} data = {this.props.data}/>}
				<CardContent id={this.props.id+"-card-content"}>
					{this.props.children}
				</CardContent>
            </div>
        );
    }
}