import React, { Component } from 'react';
import { ImageButton } from 'vhp-component-library';
import {MenuTabBar} from 'vhp-component-library'
import { ActionRow } from 'vhp-component-library';

/**
 * Creates a row of buttons for a menu bar
 * @param {Function} SetTab | the function called in the parent component when changing tabs.
 * @param {Array} tabs | an array of strings for each tab.
 * @param {Array} images | an optional array of images when giving the tabs images
 * @param {String} MenuStyle | the CSS style of the menu, added to the menu's class.
 * @param {String} id | the id of the component.
 * @param {Function} CollapseFunction | the function called when collapsing the menu, passing either true or false.
 * @param {Object} moreActions | the object giving to the moreActions ActionRow component, aligned with the menubar.
 * @param {Object} bottomActions | the object giving to the bottomActions ActionRow component.
 */
export class MenuSideBar extends Component {
    constructor(props) {
        super(props)

        this.state = {
            MenuStyle:this.props.MenuStyle,
			collapsed:false
        }

        this.SetMenuCollapse = this.SetMenuCollapse.bind(this)
    }

	componentDidUpdate(prevProps, prevState) {
		if (this.props.MenuStyle != this.state.MenuStyle) {
			if (this.props.MenuStyle == "collapsed") {
				this.setState({
					MenuStyle:"collapsed",
					collapsed:true
				})
			} else {
				this.setState({
					MenuStyle:"",
					collapsed:false
				})
			}
		}
	}

    SetMenuCollapse() {
        if (this.state.MenuStyle == "") {
            this.setState({
                MenuStyle:"collapsed",
				collapsed:true
            },()=>{
				if (this.props.CollapseFunction) {
					this.props.CollapseFunction(true)
				}
			})
        } else {
            this.setState({
                MenuStyle:"",
				collapsed:false
            },()=>{
				if (this.props.CollapseFunction) {
					this.props.CollapseFunction(false)
				}
			})
        }
    }


    render() {
        return(
            <div className={'side-bar-menu-cont ' + this.state.MenuStyle}>
				<div className='side-bar-menu-controls'>
					<ImageButton
						src={this.props.menuButton||"https://www.vhpportal.com/repo/assets/icons/menu.png"}
						ClickFunction = {this.SetMenuCollapse}
						buttonClass = "side-bar-menu"
					/>
					{!this.state.collapsed&&
						<div className='side-bar-menu-more-actions'>
							{this.props.moreActions&&<ActionRow 
								data = {this.props.moreActions}
							/>}
						</div>
					}
				</div>
                <MenuTabBar
                    SetTab = {this.props.SetTab}
                    MenuStyle = {"side-bar " + this.state.MenuStyle}
                    tabs = {this.props.tabs}
                    images = {this.props.images}
                />
				{this.props.bottomActions&&<ActionRow 
					actionRowClass = "side-bar-bottom-buttons"
					id = "side-bar-bottom-buttons"
					data = {this.props.bottomActions}
				/>}
            </div>
        )
    }
}