import React, { Component } from 'react';
import { Clone, SetProperty, TextButton, TextInput, Warning } from 'vhp-component-library';
import { DropOption } from './DropOption';

/**
 * A DropDown which allows a list to be dynamically added to and automatically selects the added option.
 */
export class DynamicDropDown extends Component {
    constructor(props) {
        super(props)

        this.state = {
            selected:this.props.selected||{text:"Choose one",value:null},
			list:this.props.list||[],
			newOpt:"",
			warningActive:false
        }

		this.SetProperty = SetProperty.bind(this)
		this.AddToList = this.AddToList.bind(this)
		this.GenerateList = this.GenerateList.bind(this)
		this.ToggleDroplist = this.ToggleDroplist.bind(this)
		this.SelectOption = this.SelectOption.bind(this)
    }

	componentDidUpdate(prevProps, prevState) {
		if (prevProps.selected.text != this.props.selected.text && prevState.selected.value == this.state.selected.value) {
			this.setState({
				selected:this.props.selected
			})
		}
    }

	/**
     * 
     * @param {object} selectedOption : selected object
     */
    SelectOption(selectedOption) {
        //If it's not already selected update it.
        if (this.state.selected.value != selectedOption.value) {
            for (let option in this.state.list) {
                if (this.state.list[option].value == selectedOption.value) {
                    this.setState({
                        selected:selectedOption
                    },()=>{
						if (this.props.ChangeFunction) {
							this.props.ChangeFunction(this.state.selected, this.props.data)
						}
                    })
                    this.ToggleDroplist();
                    break
                }
            }
        }
    }

	AddToList(text) {
		let list = Clone(this.state.list)
		for (let i = 0; i < list.length; i++) {
			if (list[i].value == text) {
				this.setState({
					warningActive:true
				})
				return 0
			}
		}
		list.push({text:text, value:text})
		this.setState({
			list:list,
			newOpt:"",
			warningActive:false
		},()=>{
			this.SelectOption({text:text, value:text})
		})
		if (this.props.AddFunction) {
			this.props.AddFunction({text:text, value:text})
		}
	}

	/**
     * Resets list and toggles drop list visibility
     */
    ToggleDroplist() {
        this.setState({
            listActive:!this.state.listActive
        })
    }

	GenerateList() {
		let items = this.state.list.map((obj) => {
			if (obj.value != this.state.selected.value) {
				return(<DropOption
                        text = {obj.text}
                        value = {obj.value}
                        id = {obj.id}
                        ClickFunction = {this.SelectOption}
                        data={obj}
                        selected={false}
                        key = {obj.value}
				    />)
			    }
		    }	
		);

		return items
    }

    render() {
        return(
            <div className='dropdown-cont dynamic'>
				<DropOption 
                    text = {this.state.selected.text}
                    value = {this.state.selected.value}
                    id = {this.state.selected.id}
                    ClickFunction = {this.ToggleDroplist}
                    selected = {true}
                    testid = {this.props.id}
                    optionClass = {this.state.dropDownClass}
                    clearVisible = {this.state.clearVisible}
                    ClearFunction = {this.ClearSelected}
                />
                {this.state.listActive&&<div className = "dropdown-list" role = "listbox">
					{this.props.searchable&&<SearchBar 
						FilterFunction = {this.FilterData}
						searchKey = {this.props.searchKey}
					/>}
					<div className='dropdown-add-cont'>
						<div className='text-box'>
							<TextInput 
								value = {this.state.newOpt}
								ChangeFunction = {this.SetProperty}
								SubmitFunction = {this.AddToList}
								data = "newOpt"
								placeholder = "Type to add to list..."
							/>
							{this.state.warningActive&&<Warning 
								color = 'red'
								message = "This item already exists on the list"
							/>}
						</div>
						<TextButton 
							text = "+"
							buttonClass = "dropdown-add-button"
							ClickFunction = {this.AddToList}
							data = {this.state.newOpt}
						/>
					</div>
					{this.GenerateList()}
				</div>}
            </div>
        );
    }
}