import React, { Component } from 'react';

/**
 * A timed gallery component that rotates between components on a timer.
 * @todo Fix the animations to sync properly and not have flashing issues
 */
export class TimedGallery extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: true,
			timer:null,
			selected:-1,
			animState:"center"
        }

        this.handleMouseEnter = this.handleMouseEnter.bind(this)
		this.handleMouseLeave = this.handleMouseLeave.bind(this)
		this.ChangeGallery = this.ChangeGallery.bind(this)
		this.GetCurrent = this.GetCurrent.bind(this)
		this.ResetAnimation = this.ResetAnimation.bind(this)
		this.SetAnimation = this.SetAnimation.bind(this)
    }

	componentDidMount() {
		if (this.state.timer === null && this.props.data.length > 0) {
			this.setState({
				timer:setTimeout(this.ChangeGallery, this.props.timer||10000),
				selected:0,
				animState:'center'
			})
		}
	}

	handleMouseEnter() {
		this.setState({
			timer:null,
			animState:'center highlight'
		})
	}

	handleMouseLeave() {
		this.setState({
			timer:setTimeout(this.ChangeGallery, this.props.timer||10000),
			animState:'center'
		})
	}

	ChangeGallery() {
		if (this.state.timer !== null) {
			let index = -1
			//Move through the class states
			/*this.setState({
				animState:"left"
			},()=>{
				setTimeout(this.ResetAnimation, 3001);
			})*/
			if (this.state.selected == (this.props.data.length - 1)) {
				this.setState({
					timer:setTimeout(this.ChangeGallery, this.props.timer||10000),
					selected:0
				})
			} else {
				this.setState({
					timer:setTimeout(this.ChangeGallery, this.props.timer||10000),
					selected:this.state.selected + 1
				})
			}
		}
	}

	ResetAnimation() {
		this.setState({
			animState:'right'
		},()=>{
			setTimeout(this.SetAnimation, 3000)
		})
	}

	SetAnimation() {
		if (this.state.selected == (this.props.data.length - 1)) {
			this.setState({
				timer:setTimeout(this.ChangeGallery, this.props.timer||10000),
				selected:0
			},()=>{
				this.setState({
					animState:"center"
				})
			})
		} else {
			this.setState({
				timer:setTimeout(this.ChangeGallery, this.props.timer||10000),
				selected:this.state.selected + 1
			},()=>{
				this.setState({
					animState:"center"
				})
			})
		}
	}

	GetCurrent(index) {
		if (index >= 0 && this.props.data[index] != undefined) {
			return <div className={'gallery-item ' + this.state.animState} id = {index}>
				{this.props.data[this.state.selected]}
			</div>
		}
	}

    render() {
        return(
            <div
				className = 'gallery-container'
				onMouseEnter = {this.handleMouseEnter} 
				onMouseLeave = {this.handleMouseLeave}
			>
				{this.GetCurrent(this.state.selected)}
            </div>
        );
    }
}