import React, { Component } from 'react';
import { InputForm, ToggleBoolean, TextButton, GetStateChanged } from 'vhp-component-library';

/**
 * A component which takes a list of items and creates a DropDown of checkboxes which can be multi-selected.
 * Cannot use listActive or selected as options
 * @param {Array} list | an array of text,value pairs describing each option
 * @param {Array} selected | an array of strings which match the value of all selected options
 * @returns {Array} | an array of text,value pairs whose matching state object is true
 */
export class MultiSelectDropDown extends Component {
    constructor(props) {
        super(props)

        this.state = {
            listActive:false,
			selected:0,
			mounted:false,
			titles:{}
        }

		this.ToggleCheckbox = this.ToggleCheckbox.bind(this)
		this.ToggleList = this.ToggleList.bind(this)
		this.GetSelected = this.GetSelected.bind(this)
		this.GetPillList = this.GetPillList.bind(this)

		if (this.props.defaultText == undefined) {
			this.defaultText = "Select options"
		} else {
			this.defaultText = this.props.defaultText
		}
    }

	/**
	 * On component mount, take the list from props and create a DropDown list, and set state
	 */
	componentDidMount() {
		let formdata = []
		let titles = {}
		let count = 0;
		for (let i = 0; i < this.props.list.length; i++) {
			let value = false;
			if (this.props.selected != undefined) {
				for (let j = 0; j < this.props.selected.length; j++) {
					if (this.props.list[i].value == this.props.selected[j].value) {
						value = true;
						count = count + 1;
						break;
					}
				}
			}
			this.setState({
				[this.props.list[i].value]:value
			},()=>{
				formdata.push({
					title:this.props.list[i].text,
					value:this.state[this.props.list[i].value],
					inputType:"Checkbox",
					data:this.props.list[i].value,
					ClickFunction:this.ToggleCheckbox
				})
				titles[this.props.list[i].value] = this.props.list[i].text
			})
		}
		this.setState({
			formdata:formdata,
			mounted:true,
			titles:titles,
			selected:count
		})
	}

	/**
	 * On component update, loop through and update formdata
	 */
	componentDidUpdate(prevProps, prevState) {
		let formUpdated = false;
		if (this.state.mounted) {
			for (let key in prevState) {
				if (key != "formdata" && key != "listActive" && key != "selected" && key != "mounted" && key != "titles") {
					if (prevState[key] != this.state[key]) {
						let formclone = JSON.parse(JSON.stringify(this.state.formdata))
						for (let i = 0; i < formclone.length; i++) {
							if (key == formclone[i].data) {
								formclone[i].value = this.state[key]
							}
							formclone[i].ClickFunction = this.ToggleCheckbox //We have to set this every time as it gets reset
						}
						this.setState({
							formdata:formclone
						})
						formUpdated = true;
					}
				}
			}
		}
	}

	ToggleCheckbox(value, key) {
		let count = 0;
		if (value == true) {
			count = this.state.selected + 1
		} else {
			count = this.state.selected - 1;
		}
		this.setState({
			[key]:value,
			selected:count
		},()=>{
			if (this.props.ChangeFunction) {
				this.props.ChangeFunction(this.GetSelected(), this.props.data)
			}
		})
	}

	GetSelected() {
		let data = this.props.list
		let returnData = []
		for (let i = 0; i < this.props.list.length; i++) {
			if (this.state[this.props.list[i].value] != undefined) {
				if (this.state[this.props.list[i].value] == true) {
					returnData.push({
						text:this.props.list[i].text,
						value:this.props.list[i].value
					})
				}
			}
		}
	
		return returnData
	}

	/**
	 * Gets the number of selected items and returns an array of pills or default text
	 */
	GetPillList() {
		if (this.state.selected == 0) {
			return this.defaultText
		} else {
			let list = []
			for (let key in this.state) {
				if (key != "formdata" && key != "listActive" && key != "selected" && key != "mounted" && key != "titles") {
					if (this.state[key] == true) {
						list.push(
							<div className='multi-select-pill' key = {key}>{this.state.titles[key]}</div>
						)
					}
				}
			}
			return <div className='multi-select-pill-list'>{list}</div>
		}
	}

	ToggleList() {
		this.setState({
			listActive:!this.state.listActive
		},()=>{
			if (this.props.ChangeFunction) {
				this.props.ChangeFunction(this.GetSelected(), this.props.data)
			}
		})
	}

    render() {
        return(
            <div className = {'multi-select-cont ' + this.state.listActive}>
				<div className = {'multi-select-selector ' + this.state.listActive}
					onClick={this.ToggleList}
				>
					{this.GetPillList()}
				</div>
				{this.state.formdata&&this.state.listActive&&<div className='multi-select-list'>
					<InputForm 
						formdata = {this.state.formdata}
					/>
				</div>}
            </div>
        );
    }
}