import React, { Component } from 'react';
import { ActionRow, Clone } from 'vhp-component-library';
import { GetDataForNestedTable, GetNestedJSON } from '../bin/vhp-tools';

export class TableRow extends Component {
    constructor(props) {
        super(props)

        this.state = {
			rowClass:"table-row"
		}

        this.MapActions = this.MapActions.bind(this)
		this.handleClick = this.handleClick.bind(this)

		this.GetRow = this.GetRow.bind(this)
		this.GetText = this.GetText.bind(this)
    }

	componentDidMount() {
		let rowClass = "table-row"
		if (this.props.ClickFunction != undefined) {
			rowClass = rowClass + " clickable"
		}
		if (this.props.rowType != undefined) {
			rowClass = rowClass + " " + this.props.rowType
		}
		if (this.props.rowClass != undefined) {
			rowClass = rowClass + " " + this.props.rowClass
		}
		this.setState({
			rowClass:rowClass
		})
	}

	componentDidUpdate(prevProps, prevState) {
		if (prevProps.rowClass != this.props.rowClass) {
			let rowClass = "table-row"
			if (this.props.ClickFunction != undefined) {
				rowClass = rowClass + " clickable"
			}
			if (this.props.rowType != undefined) {
				rowClass = rowClass + " " + this.props.rowType
			}
			if (this.props.rowClass != undefined) {
				rowClass = rowClass + " " + this.props.rowClass
			}
			this.setState({
				rowClass:rowClass
			})
		}
	}

	GetText(field, forceHeaders = false) {
		let text = ''
		if (this.props.rowType == 'header' || forceHeaders) {
			text = GetNestedJSON(field)
		} else {
			text =  GetDataForNestedTable(this.props.data, field)
		}
		return text
	}

	GetRow() {
		let row = []
		for (let i = 0; i < this.props.fields.length; i++) {
			let text = this.GetText(this.props.fields[i])
			let keyName = this.GetText(this.props.fields[i], true)
			let itemClass = "rowitem " + keyName
			if (this.props.RowItemFunction != undefined) {
				itemClass = itemClass + " clickable"
			}

			const onclick = () => {
				if (this.props.RowItemFunction != undefined) {
					this.props.RowItemFunction(this.props.fields[i])
				}
			}

			if (this.props.FormatFunctions && this.props.FormatFunctions[i] != null) {
				if (this.props.formatData && this.props.formatData[i]) {
					text = this.props.FormatFunctions[i](text, this.props.formatData[i])
				} else {
					text = this.props.FormatFunctions[i](text)
				}
			}

			row.push(
                <div 
					key = {keyName + ":" + text}
					className = {itemClass}
					onClick = {onclick}
				>
					{text}
				</div>
            )
		}

		return row
	}

    MapActions() {
        if (this.props.actions != undefined) {
			let actions = Clone(this.props.actions)
			for (let index in actions) {
				if (actions[index].data != undefined) {
					actions[index].data = {object:this.props.data,data:actions[index].data}
				} else {
					actions[index].data = this.props.data
				}
				actions[index].ClickFunction = this.props.actions[index].ClickFunction
			}
            return(
                <ActionRow data={actions} keyName = {this.props.keyName}/>
            )
        }
    }

	handleClick(e){
		if (this.props.ClickFunction) {
			this.props.ClickFunction(this.props.data)
		}
    }

    /**
     * Returns a row created from props.data
     * @returns render object
     */
    render() {
        return(
            <div className = {this.state.rowClass} id = {this.props.id} data-testid = {this.props.testid} onClick={this.handleClick}>
                {this.GetRow()}
                {this.MapActions(this.props.keyName)}
            </div>
        );
    }
}