import React, { Component } from 'react';
import { ImageButton } from '../Buttons/ImageButton';
import { TextButton } from '../Buttons/TextButton';

/**
 * Creates a row of buttons for a menu bar
 * @param {Function} SetTab | the function called in the parent component when changing tabs.
 * @param {Array} tabs | an array of strings for each tab.
 * @param {Array} images | an optional array of images when giving the tabs images
 * @param {String} MenuStyle | the CSS style of the menu, added to the menu's class.
 * @param {String} id | the id of the component.
 */
export class MenuTabBar extends Component {
    constructor(props) {
        super(props)

        this.state = {
            selected:[],
            buttons:[]
        }

        this.SetTab = this.SetTab.bind(this)
        this.RenderButtons = this.RenderButtons.bind(this)
    }

    /**
     * On mount, fill the selected
     */
    componentDidMount() {
        //Create the selected list
        let newSelected = []
        for (let i = 0; i < this.props.tabs.length; i++) {
            newSelected.push(undefined)
        }

		if (this.props.default != undefined) {
			newSelected[this.props.default] = "selected"
		} else {
			newSelected[0] = "selected"
		}
        

        this.setState({
            selected:newSelected 
        },() => {
            this.setState({
                buttons:this.RenderButtons()
            })
        });
    }

    /**
     * Set the tab in the button to allow button to be selected
     * @param {Number} TabID 
     */
    SetTab(TabID) {
        let arr = this.state.selected.slice();
        for (let i = 0; i < arr.length; i++) {
            if (i == TabID) {
                arr[i] = "selected"
            } else {
                arr[i] = undefined
            }
        }

        this.setState({
            selected:arr
        },() => {
            this.setState({
                buttons:this.RenderButtons()
            })
        });

        //Loop through and change classes

        this.props.SetTab(TabID)
    }

    RenderButtons() {
        return(this.props.tabs.map((val, key) => {
            if (this.props.images) {
                return (
                    <ImageButton
                        src = {this.props.images[key]}
                        key = {key+val} 
                        data={key}
                        text={this.props.tabs[key]}
                        ButtonType = "menu-button"
                        ClickFunction = {this.SetTab}
                        buttonClass = {this.state.selected[key]}
                        role = "tab"
                        aria-level = {val}
                    />
                )
            } else {
                return <TextButton
                        key = {key+val} 
                        data={key}
                        text={this.props.tabs[key]}
                        ButtonType = "menu-button"
                        ClickFunction = {this.SetTab}
                        buttonClass = {this.state.selected[key]}
                        role = "tab"
                        aria-level = {val}
                />
            }
        }))
    }

    render() {
        return(
            <>
                <div className = {"menu-bar" + " " + this.props.MenuStyle} id = {this.props.id} role="tablist">
                    {this.state.buttons}
                </div>
            </>
        );
    }
}