import React, { Component } from 'react';
import { List } from '../Lists/List';

/**
 * A class for implementing customized paragraphs for use in forms and other components.
 * @param {Object} data | the data used for forming the paragraph
 * @param {String} id | the id of the paragraph
 * @todo | Document the data object itself
 */
export class Paragraph extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: true
        }

        this.MapParagraphs = this.MapParagraphs.bind(this)
		this.MapHeading = this.MapHeading.bind(this)
    }

	MapHeading() {
		if (this.props.data.heading != undefined) {
			return <div className='paragraph-header'>{this.props.data.heading}</div> 
		}
	}

	MapParagraphs() {
		let list = []
		for (let key in this.props.data) {
			if (key == "list") {
				list.push(<List data = {this.props.data[key].data} type = {this.props.data[key].type}/>)
			} else if (key == "paragraph") {
				let text = ""
				for (let i in this.props.data[key]) {
					text = text + " " + this.props.data[key][i]
				}
				list.push(<div className = "paragraph-body">{text}</div>)
			} else if (key == "paragraphs") {
				for (let i = 0; i < this.props.data[key].length; i++) {
					let text = ""
					for (let j in this.props.data[key][i]) {
						text = text + " " + this.props.data[key][i][j]
					}
					list.push(<div className = "paragraph-body">{text}</div>)
					if (i != this.props.data[key].length - 1) {
						list.push(<br></br>)
					}
				}
				
			} else {
				if (key != "heading") {
					list.push(<div>Form blank!</div>)
				}
			}
		}

		return list
	}

    render() {
        return(
            <div className = "paragraph-cont" id = {this.props.id}>
				{this.MapHeading()}
                {this.MapParagraphs()}
            </div>
        );
    }
}