import React, { Component } from 'react';
import { FloatContainer } from '../Containers/FloatContainer';

/**
 * A basic loading screen which displays an image and static loading text
 * @param {Boolean} loading | determines the visibility of the loading screen
 */
export class BasicLoadingScreen extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
			<>
				{this.props.loading&&<FloatContainer containerClass="loadscreen">
					<div className="vhc-save-load-screen">
						<div className = "vhc-load-screen-item-cont shadow">
							<img className="vhc-load-screen-img" src="https://www.vhpportal.com/repo/assets/loading/Loading Circle.gif" />
							<div className="vhc-load-screen-text">{this.props.text}</div>
						</div>
					</div>
				</FloatContainer>}
			</>
        );
    }
}