import React, { Component } from 'react';
import { Card } from 'vhp-component-library';

const weekday = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];

import { Months } from 'vhp-component-library';
import { CalendarChip } from './CalendarChip';

/**
 * A week view calendar object which can display data
 * @param {Array} data | an array of arrays which contains the data you wish to be displayed
 * @param {Array} labelKeys | an array of arrays which contain the keys used in displaying data for each array
 * @param {Array} dateKeys | an array of strings which contain the keys used in getting the date for each array
 * @param {Array} colors | an array of colors describing in the colors for each array
 */
export class WeekView extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: true
        }

		this.GetWeekDates = this.GetWeekDates.bind(this)
		this.GetChips = this.GetChips.bind(this)
    }

	/**
	 * Takes in a date and retuns an array of Chips that contain the specified label from the data which match the provided date
	 * @param {String} date | the date which you wish to create chips from this.props.data out of
	 */
	GetChips(date) {
		let data = []
		//Loop through outer array
		for (let i = 0; i < this.props.data.length; i++) {
			for (let j = 0; j < this.props.data[i].length; j++) {
				//Compare the date field inside data[i] at [j] to the date given
				if (this.props.data[i][j][this.props.dateKeys[i]] != undefined) {
					if (this.props.data[i][j][this.props.dateKeys[i]].split('T')[0] == date.toISOString().split('T')[0]) {
						if (this.props.actions && this.props.actions[i]) {
							data.push(
								<CalendarChip 
									color = {this.props.colors[i]}
									data = {this.props.data[i][j]}
									labels = {this.props.labelKeys[i]}
									ClickFunction = {this.props.actions[i]}
								/>
							)
						} else {
							data.push(
								<CalendarChip 
									color = {this.props.colors[i]}
									data = {this.props.data[i][j]}
									labels = {this.props.labelKeys[i]}
								/>
							)
						}
					}
				}
			}
		}
		return (data)
	}

	/**
	 * Creates and generates a container 
	 * @param {number} startDate | the number of days away from today where you start (default = 1)
	 * @param {Date} endDate | the number of days away from today where you end (default = 6)
	 */
	GetWeekDates(startDate = -1, endDate = 7) {
		const today = new Date()
		const next = new Date()
		const yesterday = new Date(today)
		const nextWeek = new Date(next)
		const current = new Date()

		yesterday.setDate(yesterday.getDate() + startDate)
		nextWeek.setDate(nextWeek.getDate() + endDate)
		var loop = yesterday
		let data = []
		while (loop <= nextWeek) {
			let cardClass = "day-card"
			if (loop == yesterday) {
				cardClass = "day-card yesterday"
			} else if (loop.getDate() == current.getDate()) {
				cardClass = "day-card today"
			}
			if (loop.getDate() == 1) {
				data.push(
					<div className='day-card-divider'>
						<div className='divider'></div>
						<div className='month'>
							{Months[loop.getMonth()].text}
						</div>
						<div className='divider'></div>
					</div>
				)
			}
			data.push(
				<div className={cardClass} key = {loop}>
					<div className='day-info'>
						{this.GetChips(loop)}
					</div>
					<div className='day-label'>
						<div className='weekday'>{weekday[loop.getDay()]}</div>
						<div className='date'>{loop.getDate()}</div>
					</div>
				</div>
			)
			var newDate = loop.setDate(loop.getDate() + 1);
			loop = new Date(newDate);
		}

		return data
	}

    render() {
        return(
            <Card
				titlebar = {this.props.titlebar}
				title = {this.props.title}
				cardClass = "outline-card week-view"
			>
				{this.GetWeekDates(this.props.startDate, this.props.endDate)}
			</Card>
        );
    }
}