import React, { Component } from 'react';
import { BasicHeader, Card, CardContent, CheckFormFill, InputForm, SetProperty, TextButton, Clone, ToggleBoolean } from 'vhp-component-library';

let FormTitles = {
	"empID":"Employee ID",
	"type":"Account Type",
	"user":"Username",
	"permissions":"Permissions",
	"pswrd":"Password",
	"active":"Active"
}

/**
 * @todo Load data from props
 */
export class AccountEditor extends Component {
    constructor(props) {
        super(props)

        this.state = {
            empID:this.props.empID||"",
			type:"",
			user:"",
			permissions:"",
			pswrd:"",
			active:false,
			formReqs:{
				empID:"",
				type:"",
				user:""
			}
        }

		this.GetAccountForm = this.GetAccountForm.bind(this)
		this.AddAccount = this.AddAccount.bind(this)

		this.SetProperty = SetProperty.bind(this)
		this.CheckFormFill = CheckFormFill.bind(this)
		this.ToggleBoolean = ToggleBoolean.bind(this)
    }

	componentDidMount() {
		if (this.props.account) {
			let state = Clone(this.state)
			for (let key in state) {
				if (this.props.account[key] != undefined) {
					state[key] = this.props.account[key]
				}
			}

			this.setState({
				...state
			})
		}
	}

	/**
	 * @todo consolidate this to only update state in this component, not in device log
	 * @returns 
	 */
	GetAccountForm() {
		let formdata = []
		if (this.props.account) {
			for (let key in this.props.account) {
				if (key != "empID" && key != "_id" && key != "__v" && FormTitles[key] != undefined) {
					formdata.push(
						{
							title:FormTitles[key] + ":",
							inputType:"TextInput",
							value:this.state[key],
							data:key,
							ChangeFunction:this.SetProperty
						}
					)
					if (key == "active") {
						formdata[formdata.length - 1].inputType = "Checkbox"
						formdata[formdata.length - 1].ClickFunction = this.SetProperty
					}
				}
			}
		} else {
			for (let key in FormTitles) {
				formdata.push(
					{
						title:FormTitles[key] + ":",
						inputType:"TextInput",
						value:this.state[key],
						data:key,
						ChangeFunction:this.SetProperty
					}
				)
				if (key == "active") {
					formdata[formdata.length - 1].inputType = "Checkbox"
					formdata[formdata.length - 1].ClickFunction = this.SetProperty
				}
				if (this.state.formReqs[key] != undefined) {
					formdata[formdata.length - 1].formItemClass = this.state.formReqs[key]
				}
			}
		}
		

		return <InputForm 
			formdata = {formdata}
		/>
	}

	AddAccount() {
		this.setState({
			formReqs:this.CheckFormFill(this.state.formReqs, this.state)
		})

		if (this.state.devID !== "" && this.state.empID !== "") {
			this.props.AddAccount(this.state)
			this.props.CloseFunction(this.props.closeData)
		}
	}

    render() {
        return(
            <Card
				id = "account-editor"
				title = "Account Editor"
				titlebar = {true}
				actions = {{minimize:null, close:this.props.CloseFunction}}
				data = {{minimize:null, close:this.props.closeData}}
			>
				{this.props.account&&<div id = "dev-editor-user-info">
					<BasicHeader 
						headerClass = "left-align"
						text = {this.props.account.type + " - " + this.props.user.name}
					/>
					<div className='h4'>Employee #: {this.props.account.empID}</div>
				</div>}
				<CardContent cardContentClass = "device-editor-form">
					{this.GetAccountForm()}
				</CardContent>
				{!this.props.account&&<TextButton 
					text = "Add Account"
					ClickFunction = {this.AddAccount}
					data = {this.state}
				/>}
				{this.props.account&&<TextButton 
					text = "Update"
					ClickFunction = {this.props.UpdateAccount}
					data = {{...this.state,_id:this.props.account._id}}
				/>}
			</Card>
        );
    }
}