import React, { Component } from 'react';

import { DropDown } from '../Dropdowns/DropDown';
import { TextArea } from '../Inputs/TextArea';
import { TextInput } from '../Inputs/TextInput';
import { SearchBar } from '../Inputs/SearchBar';
import { TextButton } from '../Buttons/TextButton';
import { Checkbox } from '../Inputs/Checkbox';
import { MultiSelectDropDown } from '../DropDowns/MultiSelectDropDown';
import { TextInputList } from '../Inputs/TextInputList';
import { RadioSelectList } from '../Lists/RadioSelectList'
import { Warning } from '../StaticElements/Warning';
import { DropDownList } from '../DropDowns/DropDownList';
import { DynamicDropDown } from '../DropDowns/DynamicDropDown'

export class FormItem extends Component {
    constructor(props) {
        super(props)

        this.GetInput = this.GetInput.bind(this)
    }

	GetInput(inputData) {
		if (inputData.inputType == "TextInput") {
			//TODO: Remove this conditional
			if (inputData.data == undefined) {
				inputData.data = inputData.title
			}
			return (
				<TextInput 
					value = {inputData.value}
					ChangeFunction = {inputData.ChangeFunction}
					type = {inputData.type || 'text'}
					key = {inputData.title}
					data = {inputData.data}
					inputClass = {inputData.formItemClass}
					accept = {inputData.accept}
					placeholder = {inputData.placeholder}
					SubmitFunction = {inputData.SubmitFunction}
					submitData = {inputData.submitData}
					autoFocus = {inputData.autoFocus}
					disabled = {inputData.disabled}
				/>
			)
		} else if (inputData.inputType == "TextArea") {
			return (
				<TextArea
					value = {inputData.value}
					ChangeFunction = {inputData.ChangeFunction}
					key = {inputData.title}
					data = {inputData.data}
					inputClass = {inputData.formItemClass}
				/>
			)
		} else if (inputData.inputType == "DropDown"){
			return (
				<DropDown 
					list={inputData.list} 
					selected={inputData.selected}
					ChangeFunction = {inputData.ChangeFunction}
					data = {inputData.data}
					id = {inputData.id}
					dropDownClass = {inputData.formItemClass}
					canClear = {inputData.canClear}
					searchable = {inputData.searchable}
					searchKey = {inputData.searchKey}
					fallBackOption = {inputData.fallBackOption}
				/>
			)
		} else if (inputData.inputType == "MultiSelectDropDown"){
			return (
				<MultiSelectDropDown 
					list={inputData.list} 
					selected={inputData.selected}
					ChangeFunction = {inputData.ChangeFunction}
					data = {inputData.data}
					id = {inputData.id}
					dropDownClass = {inputData.formItemClass}
				/>
			)
		} else if (inputData.inputType == "DropDownList"){
			return (
				<DropDownList 
					dropDownList = {inputData.dropDownList}
					list = {inputData.list}
					AddFunction = {inputData.AddFunction}
					RemoveFunction = {inputData.RemoveFunction}
					title = {inputData.title}
					formItemClass = {inputData.formItemClass}
					id = {inputData.id}
					searchable = {inputData.searchable}
					canClear = {inputData.canClear}
					selected = {inputData.selected}
					data = {inputData.data}
				/>
			)
		} else if (inputData.inputType == "DynamicDropDown"){
			return (
				<DynamicDropDown 
					selected = {inputData.selected}
					list = {inputData.list}
					ChangeFunction = {inputData.ChangeFunction}
					AddFunction = {inputData.AddFunction}
					id = {inputData.id}
					searchable = {inputData.searchable}
					canClear = {inputData.canClear}
					data = {inputData.data}
				/>
			)
		} else if (inputData.inputType == "SearchBar") {
			return (
				<SearchBar 
					FilterFunction = {inputData.FilterFunction}
					searchKey = {inputData.searchKey}
					searchClass = {inputData.searchClass}
					filterType = {inputData.filterType}
					id = {inputData.id}
					type = {inputData.type || 'text'}
					placeholder = {inputData.placeholder}
				/>
			)
		}  else if (inputData.inputType == "TextButton") {
			return (
				<TextButton
					text = {inputData.text}
					ClickFunction = {inputData.ClickFunction}
					buttonClass = {inputData.buttonClass}
					data = {inputData.data}
					value = {inputData.value}
					id = {inputData.id}
				/>
			)
		} else if (inputData.inputType == "Checkbox") {
			return (
				<Checkbox
					ClickFunction = {inputData.ClickFunction}
					inputClass = {inputData.inputClass}
					data = {inputData.data}
					key = {inputData.title}
					id = {inputData.id}
					value = {inputData.value}
				/>
			)
		} else if (inputData.inputType == "TextInputList") {
			return (
				<TextInputList 
					data = {inputData.data}
					id = {inputData.id}
					inputClass = {inputData.inputClass}
					key = {inputData.title}
					hideEmptyList = {inputData.hideEmptyList}
					UpdateFunction = {inputData.UpdateFunction}
					emptyListText = {inputData.emptyListText}
					list = {inputData.list}
					placeholder = {inputData.placeholder}
				/>
			)
		} else if (inputData.inputType == "RadioSelectList") {
			<RadioSelectList 
				data = {inputData.data}
				id = {inputData.id}
				radioClass = {inputData.formItemClass}
				list = {inputData.list}
				selected = {inputData.selected}
			/>
		} else if (inputData.inputType == "MultiFormItem") {
			let data = [];
			for (let i = 0; i < inputData.items.length; i++) {
				data.push(this.GetInput(inputData.items[i]));
			}
			return <div className='multi-form-item'>{data}</div>
		} else {
			return (
				<div className={inputData.class}>{inputData.value}</div>
			)
		}
	}

	GetWarning() {
		if (this.props.data.warning != undefined) {
			return <Warning 
				message = {this.props.data.warning.message}
				color = {this.props.data.warning.color||"red"}
			/>
		}
	}

    render() {
        return(
            <label className={'form-info-item ' + this.props.data.formItemClass} key = {this.props.key} id = {this.props.data.id + "-cont"}>
                <span className='form-info-title' htmlFor={this.props.data.title}>{this.props.data.title}{this.GetWarning()}</span>
                {this.GetInput(this.props.data)}
            </label>
        );
    }
}