import React, { Component } from 'react';
import { ActionRow, Card, Image, TextButton } from 'vhp-component-library';

export class Notification extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
            <div className={'notification ' + this.props.notification.level}>
				<div className='notification-info'>
					<Image 
						src = {this.props.notification.icon}
					/>
					<div className='notification-text'>
						{this.props.notification.message}
					</div>
				</div>
				{this.props.DismissNotification&&<TextButton 
					buttonClass = "notification-dismiss"
					text = "X"
					ClickFunction = {this.props.DismissNotification}
					data = {this.props.notification}
				/>}
			</div>
        );
    }
}