import React, { Component } from 'react';
import { Card, CardContent, InputForm, CardTitlebar, TextButton, Clone, SetProperty, FloatContainer, ToggleBoolean } from 'vhp-component-library';
import {List} from '../../Components/VHP/List'

import '../../Styles/UserSummary.css'

var deptlist = [
	{text:"100",value:"100"},
	{text:"200",value:"200"},
	{text:"300",value:"300"},
	{text:"350",value:"350"},
	{text:"400",value:"400"},
	{text:"450",value:"450"},
	{text:"BEE",value:"BEE"},
	{text:"BCE",value:"BCE"}
]

var typelist = [
	{text:"TECH",value:"TECH"},
	{text:"CONSULTANT",value:"CONSULTANT"},
	{text:"MANAGER",value:"MANAGER"},
	{text:"DISPATCH",value:"DISPATCH"},
	{text:"OFFICE",value:"OFFICE"},
	{text:"DATAMAN",value:"DATAMAN"},
]

export function GetDropDownFromValue(value, list) {
	for (let i = 0; i < list.length; i++) {
		if (list[i].value == value) {
			console.log("MATCH FOUND",list[i])
			return list[i]
		}
	}
	if (value !== "") {
		return {text:value, value:value}
	}
	return {text:"Choose one",value:""}
}

/**
 * A UserSummary card, which allows viewing and editing user data.
 */
export class UserSummary extends Component {
    constructor(props) {
        super(props)

        this.state = {
            confirmActive:false,
			data:null,
			type:{text:"Select one", value:null},
			dept:{text:"Select one", value:null}
        }

		this.GetDevices = this.GetDevices.bind(this)
		this.GetAccounts = this.GetAccounts.bind(this)
		this.AddDevice = this.AddDevice.bind(this)

		this.ToggleConfirm = this.ToggleConfirm.bind(this)
		this.ToggleRemove = this.ToggleRemove.bind(this)

		this.SetProperty = SetProperty.bind(this)
    }

	componentDidMount() {
		let state = Clone(this.state)
		for (let key in this.props.user) {
			if (this.props.user[key] != undefined && key != "_id" && key != "__v" && key != "empID") {
				if (key == 'type') {
					state[key] = GetDropDownFromValue(this.props.user[key], typelist)
				} else {
					state[key] = this.props.user[key]
				}
			}
		}
		this.setState({
			...state
		})
	}

	componentDidUpdate(prevProps, prevState) {
		if (this.props.user.empID != prevProps.user.empID) {
			let state = Clone(this.state)
			for (let key in this.props.user) {
				if (this.props.user[key] != undefined && key != "_id" && key != "__v" && key != "empID") {
					if (key == 'type') {
						state[key] = GetDropDownFromValue(this.props.user[key], typelist)
					} else {
						state[key] = this.props.user[key]
					}
				}
			}
			console.log(state.type, "TYPE AT UpDATE")
			this.setState({
				...state
			})
		}
	}

	ToggleRemove() {
		if (this.state.data.function != undefined) {
			this.setState({
				confirmActive:false
			},()=>{
				this.state.data.function(this.state.data)
			})
		}
	}

	ToggleConfirm(data) {
		this.setState({
			confirmActive:!this.state.confirmActive,
			data:data
		})
	}

	GetDevices() {
		let list = []
		let data = []

		if (this.props.user.Device) {
			for (let i = 0; i < this.props.user.Device.length; i++) {
				list.push(this.props.user.Device[i].name)
				data.push({user:this.props.selectedUser, device:this.props.user.Device[i], function:this.props.RemoveDevice})
			}
		}

		return <List 
			list = {list}
			AddFunction = {this.AddDevice}
			EditFunction = {this.props.EditDevice}
			RemoveFunction = {this.ToggleConfirm}
			data = {data}
		/>
	}

	GetAccounts() {
		let list = []
		let data = []

		if (this.props.user.Account) {
			for (let i = 0; i < this.props.user.Account.length; i++) {
				list.push(this.props.user.Account[i].type)
				data.push({user:this.props.selectedUser, account:this.props.user.Account[i], function:this.props.RemoveAccount})
			}
		}

		return <List 
			list = {list}
			RemoveFunction = {this.ToggleConfirm}
			EditFunction = {this.props.EditAccount}
			data = {data}
		/>
	}

	AddDevice(value) {
		this.props.AddDevice({
			devID:value,
			empID:this.props.user.empID,
			name:value
		})
	}

    render() {
        return(
			<>
				<Card
					title = {"User Summary - " + this.props.user.name}
					titlebar = {true}
					cardClass = "outline-card"
					id = "user-summary-card"
				>
					<CardContent cardContentClass = "no-padding" id = "user-information-card">
						<CardContent id="user-sum-user-info" cardContentClass = "user-sum-cont">
							<CardTitlebar title = "Employee Information" id = "job-card-title"/>
							<CardContent cardContentClass = "user-summary-form">
								<InputForm
									formdata = {[
										{
											title:"Name: ", 
											value:this.state.name,
											inputType:"TextInput",
											ChangeFunction:this.SetProperty,
											data:"name"
										},
										{
											title:"First Name: ", 
											value:this.state.fName,
											inputType:"TextInput",
											ChangeFunction:this.SetProperty,
											data:"fName"
										},
										{
											title:"Last Name: ", 
											value:this.state.lName,
											inputType:"TextInput",
											ChangeFunction:this.SetProperty,
											data:"lName"
										}
									]}
								/>
							</CardContent>
						</CardContent>
						<CardContent id="user-sum-job-info" cardContentClass = "user-sum-cont">
							<CardTitlebar title = "Job Information" id = "job-card-title"/>
							<CardContent cardContentClass = "user-summary-form">
								<InputForm
									formdata = {[
										{
											title:"Job Type: ",
											inputType:"DynamicDropDown",
											ChangeFunction:this.SetProperty,
											data:"type",
											selected:this.state.type,
											list:typelist
										},
										{
											title:"Department: ", 
											value:this.state.dept,
											inputType:"DynamicDropDown",
											ChangeFunction:this.SetProperty,
											data:"dept",
											selected:this.state.dept,
											list:deptlist
										},
										{
											title:"Reports To: ", 
											value:this.state.repTo,
											inputType:"TextInput",
											ChangeFunction:this.SetProperty,
											data:"repTo"
										},
										{
											title:"Title: ", 
											value:this.state.title,
											inputType:"TextInput",
											ChangeFunction:this.SetProperty,
											data:"title"
										},
										{
											title:"Job Description: ", 
											value:this.state.jobDesc,
											inputType:"TextInput",
											ChangeFunction:this.SetProperty,
											data:"jobDesc"
										}
									]}
								/>
							</CardContent>
						</CardContent>
						<div className='action-row'>
							<TextButton 
								text = "Update"
								ClickFunction = {this.props.UpdateEmployee}
								data = {{update:this.state,user:this.props.user}}
							/>
						</div>
					</CardContent>
					<CardContent id = "user-sum-device-info" cardContentClass = "user-sum-cont">
						<CardTitlebar title = "Devices" id = "job-card-title"/>
						<CardContent cardContentClass = "user-summary-form">
							{this.GetDevices()}
						</CardContent>
					</CardContent>
					<CardContent id = "user-sum-account-info" cardContentClass = "user-sum-cont">
						<CardTitlebar title = "Accounts" id = "job-card-title"/>
						<CardContent cardContentClass = "user-summary-form">
							{this.GetAccounts()}
							<div className='action-row'>
								<TextButton 
									text = "Add Account"
									ClickFunction = {this.props.ToggleAddAccount}
									data = {this.props.user.empID}
								/>
							</div>
						</CardContent>
					</CardContent>
					<TextButton 
						text = "Delete User"
						ClickFunction = {this.ToggleConfirm}
						data = {{function:this.props.RemoveEmployee,data:this.props.user}}
					/>
				</Card>
				{this.state.confirmActive&&<FloatContainer>
					<Card
						titlebar = {true}
						title = "Confirmation"
						actions = {{minimize:null, close:this.ToggleConfirm}}
						data = {{minimize:null, close:null}}
						id = "user-sum-remove-confirm"
					>
						<div className='text'>
							Are you sure you wish to delete the item? This action cannot be undone.
						</div>
						<div className='action-row center'>
							<TextButton 
								text = "Yes, confirm"
								ClickFunction = {this.ToggleRemove}
								id = "confirm-delete-button"
							/>
							<TextButton 
								text = "No, go back"
								ClickFunction = {this.ToggleConfirm}
								id = "go-back-button"
							/>
						</div>
					</Card>
				</FloatContainer>}
			</>
        );
    }
}