import React, { Component } from 'react';
import { TextButton } from '../Buttons/TextButton';

/**
 * A component which creates a drawable canvas element
 */
export class CanvasPad extends Component {
    constructor(props) {
        super(props)

        this.state = {
			paint:false,
			filled:false
        }

		this.canvasRef = React.createRef()

		this.coord = {x:0, y:0}

        this.handlePointerDown = this.handlePointerDown.bind(this)
		this.handlePointerUp = this.handlePointerUp.bind(this)
		this.handlePointerMove = this.handlePointerMove.bind(this)

		this.StartPainting = this.StartPainting.bind(this)
		this.StopPainting = this.StopPainting.bind(this)
		this.GetPainting = this.GetPainting.bind(this)
		this.Sketch = this.Sketch.bind(this)
		this.GetPosition = this.GetPosition.bind(this)
		this.ClearPad = this.ClearPad.bind(this)
		this.Resize = this.Resize.bind(this)

		window.addEventListener('resize', this.Resize);

		this.ctx = undefined;
    }

	componentDidMount() {
		this.setState({
			ctx:this.canvasRef.current.getContext('2d')
		})
		this.ctx = this.canvasRef.current.getContext('2d');
	}

	Resize() {
		let ratio = Math.max(window.devicePixelRatio || 1,1);
    	this.ctx.canvas.width = this.canvasRef.current.offsetWidth * ratio;
    	this.ctx.canvas.height = this.canvasRef.current.offsetHeight * ratio;
    	this.canvasRef.current.getContext('2d').scale(ratio, ratio);
	}

	GetPosition(event) {
		this.coord.x = event.nativeEvent.offsetX;
    	this.coord.y = event.nativeEvent.offsetY;
	}

	StartPainting(event) {
		if (!this.state.filled) {
			this.setState({
				filled:true
			})
			if (this.FillFunction) {
				this.FillFunction()
			}
		}
		this.setState({
			paint:true
		})
		this.GetPosition(event);
	}

	StopPainting(event) {
		this.setState({
			paint:false
		},()=>{
			if (this.props.SaveFunction) {
				this.props.SaveFunction(this.GetPainting())
			}
		})
	}

	GetPainting() {
		this.ctx.putImageData(this.ctx.getImageData(0,0,this.ctx.canvas.width, this.ctx.canvas.height), 0, 0);
	
		var image = new Image();
		image.src = this.ctx.canvas.toDataURL();
		return image;
	}

	Sketch(event) {
		if (!this.state.paint) return;
		this.ctx.beginPath();
		this.ctx.lineWidth = 3;
		this.ctx.lineCap = 'round';
		this.ctx.strokeStyle = 'black';
		this.ctx.moveTo(this.coord.x, this.coord.y);
		this.GetPosition(event);
		this.ctx.lineTo(this.coord.x , this.coord.y);
		this.ctx.stroke();
	}

	/**
	 * Clears the pad of the current signature and calls an external clear function if provided
	 */
	ClearPad() {
		this.ctx.clearRect(0,0,this.ctx.canvas.width,this.ctx.canvas.height);
		this.setState({
			filled:false
		})
		if (this.ClearFunction) {
			this.ClearFunction(false)
			if (this.props.SaveFunction) {
				this.props.SaveFunction(undefined)
			}
		}
	}

    /**
     * Click event handler
     * Can optionally pass data using props.data
     */
    handlePointerDown(e) {
        this.StartPainting(e)
    }

	handlePointerUp(e) {
        this.StopPainting(e)
	}

	handlePointerMove(e) {
		if (this.state.paint) {
        	this.Sketch(e)
		}
	}

    render() {
        return (
			<>
				<canvas 
					ref={this.canvasRef} 
					width="544.55px"
					height="100px"
					id = {this.props.id}
					onPointerDown={this.handlePointerDown}
					onPointerUp={this.handlePointerUp}
					onPointerMove={this.handlePointerMove}
					onResize={this.Resize}
				/>
				<TextButton 
					text = "Clear Signature"
					ClickFunction = {this.ClearPad}
					id = "clear-canvas"
				/>
			</>
        );
    }
}