import React, { Component } from 'react';
import { FormItem } from 'vhp-component-library';

/**
 * A DropDown used for sorting objects. Takes a list and SortFunction
 * @param {Function} SortFunction | the function used in sorting, called on changing the drop.
 * @param {Array} list | an array of text, value pairs
 */
export class SortDropDown extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
			<FormItem 
				data = {{
					inputType:"DropDown",
					list:this.props.list,
					ChangeFunction:this.props.SortFunction,
					selected:this.props.list[0],
					title:"Sort By:",
					formItemClass:"sort-drop-cont",
					id:this.props.id
				}}	
			/>
        );
    }
}