import React, { Component } from 'react';
import { TableRow } from '../TableRow';



export class Table extends Component {
    constructor(props) {
        super(props)

        this.state = {
			active:false,
			order:1,
			filter:""
		}

		this.SortData = this.SortData.bind(this)
		this.ToggleSort = this.ToggleSort.bind(this)
		this.GetRowClass = this.GetRowClass.bind(this)
    }

	SortData(data) {
		if (this.props.SortFunction) {
			return this.props.SortFunction(data, this.state.order, this.state.filter)
		}

		return data
	}
	
	ToggleSort(filter) {
		let order = 1
		if (this.state.order == -1) {
			order = 1
		} else {
			order = -1
		}

		this.setState({
			order:order,
			filter:filter
		})
	}

	GetRowClass(data) {
		if (this.props.RowClassFunction != undefined) {
			return this.props.rowClass + " " + this.props.RowClassFunction(data)
		} else {
			return this.props.rowClass
		}
	}

    render() {
        return (
			<>
				{this.props.headers&&<TableRow 
					data = {this.props.fields} 
					fields = {this.props.fields}
					rowType = "header"
					rowClass = {this.props.rowClass}
					id = {this.props.headerID}
					RowItemFunction = {this.props.SortFunction&&this.ToggleSort||undefined}
					rowItemData = {this.props.sortData}
				/>}
				<div className='tablerow-container' id = {this.props.id}>
					{this.props.FilterFunction&&this.props.FilterFunction(this.SortData(this.props.data), this.props.filterData).map((val, key) => {
						return (
							<TableRow 
								key = {key}
								keyName = {key}
								data={val}
								id = {this.props.id}
								testid = {this.props.id}
								ClickFunction = {this.props.ClickFunction}
								FormatFunctions = {this.props.FormatFunctions}
								formatData = {this.props.formatData}
								actions = {this.props.actions}
								fields = {this.props.fields}
								rowType = "normal"
								rowClass = {this.GetRowClass(val)}
							/>
						)
                    })}
					{!this.props.FilterFunction&&this.SortData(this.props.data).map((val, key) => {
						return (
							<TableRow 
								key = {key}
								keyName = {key}
								data = {val}
								id = {this.props.id}
								testid = {this.props.id}
								ClickFunction = {this.props.ClickFunction}
								FormatFunctions = {this.props.FormatFunctions}
								formatData = {this.props.formatData}
								actions = {this.props.actions}
								fields = {this.props.fields}
								rowType = "normal"
								rowClass = {this.GetRowClass(val)}
							/>
						)
                    })}
				</div>
			</>
		)
    }
}