import React, { Component } from 'react';
import { TextButton } from './TextButton';
import { SearchBar } from 'vhp-component-library';
import { ImageButton } from 'vhp-component-library';


/**
 * Takes an array of actions and creates a set of Action Buttons.
 * @param {String} actionRowClass | the class of the outer container.
 * @param {Object} data | the data used to create each button, of the format:
 * [
 * 		{
 * 			text 			: string
 * 			id   			: string
 * 			ClickFunction 	: function()
 * 			type			: type of object to create
 * 			className		: the class name to be added to the button [not implemented yet]
 * 			data			: the data passed in to the ClickFunction
 * 		}
 * 	]
 */
export class ActionRow extends Component {
	constructor(props) {
		super(props)

		this.MapActionData = this.MapActionData.bind(this)
	}

	MapActionData() {
		const data = this.props.data
		const items = data.map((obj) => {
			if (obj.type == "ActionButton" || obj.type == undefined || obj.type == "TextButton") {
				return(<TextButton
					text = {obj.text}
					id = {obj.id}
					ClickFunction = {obj.ClickFunction}
					buttonClass = {obj.buttonClass}
					data = {obj.data}
					key = {obj.keyName+obj.text}
				/>)
			} else if (obj.type == "SearchBar") {
				return(<SearchBar
                    FilterByText = {obj.FilterByText}
					placeholder = {obj.placeholder}
					searchKey = {obj.searchKey}
                />)
			} else if (obj.type == "ImageButton") {
				return(<ImageButton
					text = {obj.text}
					id = {obj.id}
					ClickFunction = {obj.ClickFunction}
					buttonClass = {obj.buttonClass}
					data = {obj.data}
					key = {obj.keyName+obj.text}
					src = {obj.src}
				/>)
			}
		}
			
		);

		return items
	}

	/**
	 * Returns an Action Row
	 * @returns render object
	*/
	render() {
		return(
			<div className = {"action-row " + this.props.actionRowClass} id = {this.props.id}>
				{this.MapActionData()}
			</div>
		);
	}
}