import React, { Component } from 'react';

/**
 * A header of a paragraph.
 * @param {String} title | the text in the header
 */
export class SectionHeader extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
            <div className='section-header'>{this.props.title}</div>
        );
    }
}