import React, { Component } from 'react';

export class Accordion extends Component {
	constructor(props) {
        super(props)

        this.state = {
            active: true,
            currentTab:0,
			collapsed:"",
			left:[],
			right:[]
        }
        
        this.SetTab = this.SetTab.bind(this)
		this.GetPage = this.GetPage.bind(this)
		this.GetLeftTabs = this.GetLeftTabs.bind(this)
		this.GetRightTabs = this.GetRightTabs.bind(this)
		this.GetTab = this.GetTab.bind(this)
    }

	componentDidMount() {
		if (this.props.left != undefined && this.props.right != undefined) {
			this.setState({
				left:this.props.left,
				right:this.props.right,
				currentTab:this.props.left[this.props.left.length - 1] + 1
			})
		} else {
			let right = []
			for (let i = 0; i < this.props.tabs.length; i++) {
				right.push(i)
			}
			this.setState({
				right:right
			})
		}
	}

	SetTab(id) {
		console.log("Setting tab:", id)
		let left = []
		let right = []
		for (let i = 0; i < this.props.tabs.length; i++) {
			if (i < id) {
				left.push(i)
			} else {
				right.push(i)
			}
		}
		this.setState({
			left:left,
			right:right,
			currentTab:id
		},()=>{
			if (this.props.SaveFunction) {
				this.props.SaveFunction(left, right)
			}
		})
	}

	GetPage() {
		return this.props.data[this.props.currentTab]
	}

	GetRightTabs() {
		if (this.state.right.length == 0) {
			return <></>
		} else {
			let tabs = []
			for (let i = 0; i < this.state.right.length; i++) {
				tabs.push(this.GetTab(this.props.tabs[this.state.right[i]], this.state.right[i]))
			}

			return <div className='accordion-tab-container right'>
				{tabs}
			</div>
		}
	}

	GetLeftTabs() {
		if (this.state.left.length == 0) {
			return <></>
		} else {
			let tabs = []
			for (let i = 0; i < this.state.left.length; i++) {
				tabs.push(this.GetTab(this.props.tabs[this.state.left[i]], this.state.left[i]))
			}

			return <div className='accordion-tab-container left'>
				{tabs}
			</div>
		}
	}

	GetTab(tab, index) {
		const onclick  = () => {
			this.SetTab(index)
		}
		let tabClass = 'accordion-tab'
		if (index == this.state.currentTab) {
			tabClass = 'accordion-tab selected'
		}
		return <div className={tabClass} onClick={onclick}>
			{tab.title}
		</div>
	}

	render() {
		return(
			<div className={'accordion-container ' + this.props.color||"red"}>
				{this.GetLeftTabs()}
				<div className='accordion-content-container'>
					{this.props.data[this.state.currentTab]}
				</div>
				{this.GetRightTabs()}
			</div>
		)
	}
}