const https = require('https');
const fetch = require('node-fetch');


console.log('CORE IS BEING TESTED')

const VAPIclient=()=>{
    this.aauth=(a=null)=>{
        !a&&(a={})
        return {
            user:a.user||'',
            pswrd:a.pswrd||'',
            coid:a.coid||'01'
        }
    }

    this.init=false,
    this.connected=false,
    this.auth=this.aauth();


    this.SETauth=(auth=null)=>{
        auth=this.aauth(auth);
        try{localStorage.setItem(JSON.stringify(auth))}catch{}
        this.auth=JSON.parse(JSON.stringify(auth));
        this.init=true;
    }
    this.GETauth=()=>{
        try{
            let vcauth=JSON.parse(localStorage.getItem('vapi-user'));
            if(vcauth.user!=undefined&&vcauth.pswrd!=undefined){this.auth=vcauth;}
            console.log(...this.auth);
        }catch{}
        return {...this.auth}//may want to send copy
    }
    this.CHECKconnection=()=>{return this.connected}
    this.CHANGEconnection=(connected=false)=>{this.connected=connected}
    this.GETstatus=()=>{return {
        init:this.init,
        connection:this.connected,
        auth:{...this.auth}
    }}
    return this
}

let sharedVC=VAPIclient();

console.log(sharedVC)

/* VAPI Core
    Base class for making connections through the VAPI. 
*/

class Core {
    /**
     * @param {{user:'',pswrd:'',coid:''}} auth can be passed to set auth
     * @param {Boolean} sync will connect with auth activity from other Cores
     * @param {String | Boolean} host can change the host has need, FALSE->default 'https://www.vhpportal.com/
     * @param {
     *  
     * } config
     * @param {Object} dev object {comments:TRUE | FALSE} can add flags for better control when developing 
     */
    constructor(
        {
            auth=null,
            sync=true,
            host=false,
            dev=false,
            config=false,
            client=true
        }
        ){
        this.connected = false;
        this.host = host||'https://www.vhpportal.com/';//take out defualt, and require pass
        this.client=client;
        this.dev=dev;
        this.sync=sync;
        this.config=config;
        this.auth=auth||{
            user:'',
            pswrd:'',
            coid:''
        };
        if(sync){
            sharedVC.GETstatus().init?
            (this.SYNCauth(true)):
            (this.SYNCauth());
        }
        this.dev.https&&(process.env.NODE_TLS_REJECT_UNAUTHORIZED=0);
        //setup ping cycle to check for connections
        this.CHECKconnect();
    }

    CHECKconnect(){
        var trigger = false;
        let again=()=>{
            if(!trigger){
                trigger=true;
                this.SYNCauth();
                this.CHECKconnect();
                return true;
            }else{return false}
        }
        setTimeout(()=>{
            fetch('https://www.example.com',{mode:'no-cors'})
            .then(res=>{this.connected=true})
            .catch(err=>{this.connected=false})
            .then(answr=>{again()});

            //timer incase drop connection
            setTimeout(()=>{if(again()){this.connected=false;}},1500);
        },2000);
    }

    /** PING vhpportal
     *  A way to check if there is a connection to the portal. will retrun false.
     * 
    */
    Ping(full=false){
        return new Promise((resolve,reject)=>{
            this.SENDrequest({}).then(answr=>{
                this.dev.comments&&console.log(answr);
                this.connected = answr.err?false:true;
                return resolve(answr.success?
                (full?{
                    call:answr,
                    status:this.GETstatus()
                }:answr.msg):
                (full?{
                    call:answr,
                    status:this.GETstatus()
                }:this.connected))
            });
        });
    }

    /** Login vhpportal
     * The login is a way to check authentication, and is the
     * only way to reset authentication after construction
     * 
     * @param {user:String, pswrd:String} user 
     * @returns true || false
     */
    Login(user=null){
        return new Promise((resolve,reject)=>{
            if(user){
                this.auth.user = user.user||'';
                this.auth.pswrd = user.pswrd || '';
            }
            this.SENDrequest({route:'LOGIN'}).then(
                answr=>{
                    this.dev.comments&&console.log('Login Response >',answr);
                    this.connected = answr.err?false:true;
                    if(!answr.success){
                        this.auth.user='';
                        this.auth.pswrd='';
                        return resolve(false);
                    }
                    this.sync&&this.SYNCauth();
                    return resolve(answr.success || false);
                }
            )
        })
    }
    /**
     * Get the satus of this core and the shared status if synced
     * @returns {
     *  syncing: true || false
     *  shared: null || {shared status}
     *  auth: {user:String, pswrd:String, coid:String}
     *  connected: true || false
     * }
     */
    GETstatus(connected=false){
        return connected?this.connected:{
            syncing:this.sync,
            shared:this.sync?sharedVC.GETstatus:null,
            auth:{...this.auth},
            connected:this.connected
        }
    }

    /**
     * Get the user data stroed in this core
     * @returns {user:String, pswrd:String, coid:String}
     */
    GETauth(){return {...this.auth}}

    /**
     * Sync the auth either:
     *  from:true = Get from shared
     *  from:false = Set shared
     * @param {Boolean} from 
     * @returns true
     */
    SYNCauth(from=false){
        from?
        (this.auth=sharedVC.GETauth())&&(this.connected=sharedVC.CHECKconnection()):
        (sharedVC.SETauth(this.auth))&&(sharedVC.CHANGEconnection(this.connected));
        return true;
    }

    SENDrequest({
        body=false,
        pack={},
        route='PING',
        request='',
        url=this.host+'api/'
    }){
        return new Promise((resolve,reject)=>{
            this.GETstatus
            if(this.client){return resolve(this.REQUESTclient({
                body:body,
                pack:pack,
                route:route,
                request:request,
                url:url
            }));}
            else{
                return resolve(this.REQUESTserver({
                    body:body,
                    pack:pack,
                    route:route,
                    request:request,
                    url:url
                }))
            }
        });
    }

    REQUESTclient({
        body=false,
        pack={},
        route='PING',
        request='',
        url=this.host+'api/'
    }){
        return new Promise((resolve,reject)=>{
            this.sync&&route!=='LOGIN'&&this.SYNCauth(true);
            console.log(this.auth)
            let options={
                method:'POST',
                headers:{
                    'Accept':'application/json'
                },
                body:body?JSON.stringify(body):JSON.stringify({
                access:{
                    user:this.auth.user||'',
                    pswrd:this.auth.pswrd||'',
                    coid:'01',//this.auth.coid||'',
                    request:request
                },
                pack:pack,
                })
            }
            this.dev.comments&&console.log('SENDING REQUEST->',route);
            fetch(body?url:url+route,options)
            .then(response=>{
                this.dev.comments&&console.log('Before Pars>',response)
                return response.json()
            })
            .then(data=>{
                this.dev.comments&&console.log('Response Data>',data);
                return resolve(data);
            })
            .catch(err=>{return resolve({msg:'Request Failes',success:false,err:err});});
        });
    }

    REQUESTserver({
        body=false,
        pack={},
        route='PING',
        request=''
    }){
        return new Promise((resolve,reject)=>{
            let bod = body?JSON.stringify(body):JSON.stringify({
                access:{
                    user:this.auth.user||'',
                    pswrd:this.auth.pswrd||'',
                    coid:'01',//this.auth.coid||'',
                    request:request
                },
                pack:pack,
            });
            var options = {
                hostname: 'www.vhpportal.com',//this.host,
                port: 443,
                path: `/api/${route}/`,
                method: 'POST',
                agent:new https.Agent({rejectUnauthorized:false}),
                headers: {
                'Content-Type': 'application/json',
                'Content-Length': Buffer.byteLength(bod)
                }
            };
            //change to http for local testing
            var req = https.request(options, function (res) {
                res.setEncoding('utf8');
            
                var data = '';
            
                res.on('data', function (chunk) {
                data = data + chunk;
                });
            
                res.on('end',function(){
                    try{
                        data = JSON.parse(data);
                        return resolve(data);
                    }catch{return resolve({success:false,err:'bad parse'})}
                });
                req.on('error', function (e) {
                    console.log("Error : " + e.message);
                    return resolve({success:false,err:e});
                });
            });
            req.on('error',(err)=>{
                console.log('THIS errorerd >',err);
                return resolve({success:false,err:err})
            });
            // write data to request body
            req.write(bod);
            req.end();
        })
    }

    //PREPpack({pack,type})
    TESTsupportflow(st){
        let suppticket=(s=null)=>{
            if(!s){s={}}
            return{
                name:s.name||'',
                strtdate:s.strtdate||'',
                duedate:s.duedate||'',
                request:s.request||'',
                requestfor:s.requestfor||'',
                priority:s.priortity||10,
                descr:s.descr||'',
                dep:s.dep||''
            }
        }
        this.SENDrequest({
            body:suppticket(st),
            url:'https://prod-98.westus.logic.azure.com:443/workflows/10f5d3ecf3fe4ba5a97a4577c7405a3e/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=4Br5Z6VhFsIvy-B3KrA42ISM8Y8pLTdqKzo4_z9y3R0'
        }).then(answr=>{console.log(answr);});
    }
}
module.exports={Core}