import React, { Component } from 'react';
import { Clone, FormItem, List } from 'vhp-component-library';

/**
 * A component used for adding items to a list from a DropDown list. Note that it saves as text,value pairs and expects
 * text,value pairs. If you do not wish to save the list as a text,value pair, wrap the this.props.list in a function
 * when passing it in that generates a dropdown list.
 */
export class DropDownList extends Component {
    constructor(props) {
        super(props)

		this.state = {
			dropDownList:this.props.dropDownList
		}

		this.AddToList = this.AddToList.bind(this)
		this.RemoveFromList = this.RemoveFromList.bind(this)
    }

	componentDidMount() {
		if (this.props.list.length > 0) {
			for (let i = 0; i < this.props.list.length; i++) {
				this.AddToList(this.props.list[i], false)
			}
		}
	}

	/**
	 * Calls this.props.AddFunction and then removes from the list in state.
	 * @param {*} value 
	 * @param {*} key 
	 */
	AddToList(value, callAdd = true) {
		if (value.value != null) {
			if (callAdd) {
				this.props.AddFunction(value)
			}

			let list = Clone(this.state.dropDownList)
			for (let i = 0; i < list.length; i++) {
				if (list[i].text == value.text && list[i].value == value.value) {
					list.splice(i, 1)
					break;
				}
			}
			this.setState({
				dropDownList:list
			})
		}
	}

	/**
	 * Calls this.props.RemoveFunction() and then re-adds to the list in state
	 */
	RemoveFromList(value, key) {
		if (value.value != null) {
			this.props.RemoveFunction(value)
			let list = Clone(this.state.dropDownList)
			for (let i = 0; i < this.props.dropDownList.length; i++) {
				if (this.props.dropDownList[i].text == value.text && this.props.dropDownList[i].value == value.value) {
					list.push(this.props.dropDownList[i])
					break;
				}
			}
			this.setState({
				dropDownList:list
			})
		}
		
	}

    render() {
        return(
            <div className='dropdown-list-container'>
				<FormItem 
					data = {{
						inputType:"DropDown",
						id:this.props.id,
						title:this.props.title,
						selected:this.props.selected||{text:"Select an option", value:null},
						list:this.state.dropDownList,
						ChangeFunction:this.AddToList,
						canClear:this.props.canClear,
						formItemClass:this.props.formItemClass,
						searchable:this.props.searchable
					}}
				/>
				<List 
					list = {this.props.list}
					RemoveFunction = {this.RemoveFromList}
				/>
			</div>
        );
    }
}