import React, { Component } from 'react';
import { ActionRow } from './ActionRow';
import { TextButton } from './TextButton';

/**
 * A floating action button which can be clicked to expand a list of actions.
 * @param {String} position | the position of the button on the screen. defaults bottom-left.
 * @param {String} id | the id of the outer container
 * @param {Object} data | the object used in creating the ActionRow component shown on click of the FAB.
 */
export class FloatingActionButton extends Component {
    constructor(props) {
        super(props)

		this.state = {
			toggled:false,
			class:""
		}

		this.ToggleActions = this.ToggleActions.bind(this)
		if (!this.props.position) {
			this.position = "bottom-left"
		} else {
			this.position = this.props.position
		}
    }

	ToggleActions() {
		this.setState({
			toggled:!this.state.toggled,
			class:"toggled"
		},()=>{
			if (this.state.toggled == true) {
				this.setState({
					class:"toggled "
				})
			} else {
				this.setState({
					class:""
				})
			}
		})
	}

    render() {
        return(
            <div className={'fab-container ' + this.state.class + this.position} id = {this.props.id}>
				{this.state.toggled&&
					<ActionRow 
						actionRowClass = "floating-list"
						data = {this.props.data}
					/>
				}
				<TextButton 
					buttonClass = "floating"
					text = "+"
					ClickFunction = {this.ToggleActions}
				/>
			</div>
        )
    }
}