import React, { Component } from 'react';

export class CalendarChip extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: true
        }

        this.handleClick = this.handleClick.bind(this)
        this.GetChipData = this.GetChipData.bind(this)
		this.GetClickClass = this.GetClickClass.bind(this)
    }

    /**
     * Click event handler
     * Can optionally pass data using props.data
     */
    handleClick(e){
        this.props.ClickFunction(this.props.data)
    }

	GetClickClass() {
		if (this.props.ClickFunction != undefined) {
			return " clickable"
		} else {
			return ""
		}
	}

	GetChipData() {
		let data = []
		for (let i = 0; i < this.props.labels.length; i++) {
			if (this.props.data[this.props.labels[i]] != undefined) {
				data.push(
					<div className={'chip-' + this.props.labels[i]}>
						{this.props.data[this.props.labels[i]]}
					</div>
				)
			}
			
		}

		return data
	}

    render() {
        return(
            <div className={'chip ' + this.props.color + this.GetClickClass()} onClick={this.handleClick}>
                {this.GetChipData()}
            </div>
        );
    }
}