import React, { Component } from 'react';
import { Card, Clone, FloatContainer, GetDropDownFromText, GetDropDownFromValue, InputForm, SetProperty, TextButton } from 'vhp-component-library';

export class DataFilterCard extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: false
        }

        this.GenerateForm = this.GenerateForm.bind(this)
		this.SetProperty = SetProperty.bind(this)
		this.SetNestedProperty = this.SetNestedProperty.bind(this)
		this.SetFilters = this.SetFilters.bind(this)
		this.ResetFilters = this.ResetFilters.bind(this)
    }

	componentDidMount() {
		let state = Clone(this.state)
		for (let index in this.props.data) {
			if (this.props.data[index].type == 'DateRange') {
				state[this.props.data[index].key] = {
					start:"",
					end:""
				}
				if (this.props.filters[this.props.data[index].key] != "") {
					state[this.props.data[index].key].start = this.props.filters[this.props.data[index].key].start.split('T')[0];
					state[this.props.data[index].key].end = this.props.filters[this.props.data[index].key].end.split('T')[0];
				}
			} else if (this.props.data[index].type == 'NumRange') {
				state[this.props.data[index].key] = {
					start:"",
					end:""
				}
				if (this.props.filters[this.props.data[index].key] != "") {
					state[this.props.data[index].key].start = this.props.filters[this.props.data[index].key].start
					state[this.props.data[index].key].end = this.props.filters[this.props.data[index].key].end
				}
			} else if (this.props.data[index].type == 'DropDown') {
				state[this.props.data[index].key] = {text:"Select an option", value:""}
				if (this.props.filters[this.props.data[index].key] != "") {
					state[this.props.data[index].key] = GetDropDownFromValue(this.props.filters[this.props.data[index].key], this.props.data[index].list)
				}
			} else if (this.props.data[index].type == 'Length') {
				state[this.props.data[index].key] = false
				if (this.props.filters[this.props.data[index].key] !== "") {
					state[this.props.data[index].key] = this.props.filters[this.props.data[index].key]
				}
			} else {
				state[this.props.data[index].key] = ""
				if (this.props.filters[this.props.data[index].key] != "") {
					state[this.props.data[index].key] = this.props.filters[this.props.data[index].key]
				}
			}
		}

		state.active = true
		this.setState({...state})
	}

	SetNestedProperty(value, key) {
		let prop = Clone(this.state[key.key])
		prop[key.nest] = value
		this.setState({
			[key.key]:prop
		})
	}

    GenerateForm(data) {
		let formdata = []
		for (let index in data) {
			switch (data[index].type) {
				case "DropDown":
					formdata.push({
						title:data[index].title,
						inputType:"DropDown",
						selected:this.state[data[index].key],
						data:data[index].key,
						list:data[index].list,
						formItemClass:"form-item-basic",
						ChangeFunction:this.SetProperty,
						searchable:true
					})	

					break;
				case "Date":
					formdata.push({
						title:data[index].title,
						inputType:"TextInput",
						type:"date",
						value:this.state[data[index].key],
						data:data[index].key,
						formItemClass:"form-item-basic",
						ChangeFunction:this.SetProperty
					})	

					break;
				case "Length":
					formdata.push({
						title:data[index].title,
						inputType:"Checkbox",
						value:this.state[data[index].key],
						data:data[index].key,
						formItemClass:"form-item-basic",
						ClickFunction:this.SetProperty
					})	

					break;
				case "DateRange":
					formdata.push({
						title:data[index].title + " (Range)",
						inputType:"TextInput",
						type:"date",
						value:this.state[data[index].key].start,
						data:{key:data[index].key, nest:"start"},
						formItemClass:"form-item-basic",
						ChangeFunction:this.SetNestedProperty
					})
					formdata.push({
						title:"",
						inputType:"TextInput",
						type:"date",
						value:this.state[data[index].key].end,
						data:{key:data[index].key, nest:"end"},
						formItemClass:"form-item-basic",
						ChangeFunction:this.SetNestedProperty
					})	

					break;
				case "NumRange":
					formdata.push({
						title:data[index].title + " (Range)",
						inputType:"TextInput",
						type:"number",
						value:this.state[data[index].key].start,
						data:{key:data[index].key, nest:"start"},
						formItemClass:"form-item-basic",
						ChangeFunction:this.SetNestedProperty
					})
					formdata.push({
						title:"",
						inputType:"TextInput",
						type:"number",
						value:this.state[data[index].key].end,
						data:{key:data[index].key, nest:"end"},
						formItemClass:"form-item-basic",
						ChangeFunction:this.SetNestedProperty
					})
					break;
				default:
					break;
			}
		}

		return <InputForm 
			formdata = {formdata}
		/>
	}

	SetFilters(data) {
		let retval = {}
		for (let index in data) {
			if (data[index].type == 'DropDown') {
				retval[data[index].key] = this.state[data[index].key].value
			} else if (data[index].type == 'Date') {
				retval[data[index].key] = new Date(this.state[data[index].key]).toISOString()
			} else if (data[index].type == 'DateRange') {
				if (this.state[data[index].key].start != "" && this.state[data[index].key].end != "") {
					retval[data[index].key] = {
						start:new Date(this.state[data[index].key].start).toISOString(),
						end:new Date(this.state[data[index].key].end).toISOString()
					}
				}
			} else if (data[index].type == 'NumRange') {
				if (this.state[data[index].key].start != "" && this.state[data[index].key].end != "") {
					retval[data[index].key] = {
						start:new Number(this.state[data[index].key].start),
						end:new Number(this.state[data[index].key].end)
					}
				}
			} else {
				retval[data[index].key] = this.state[data[index].key]
			}
		}
		console.log("RETURN FROM FILTER CARD", retval)
		this.props.SetFilters(retval)
		this.props.CloseFunction(this.props.closeData)
	}

	ResetFilters() {
		this.props.ResetFilters()
		this.props.CloseFunction(this.props.closeData)
	}

    render() {
        return(
			<Card
				title = {this.props.title + " Filters"}
				titlebar = {true}
				data = {{minimize:null, close:this.props.closeData}}
				actions = {{minimize:null, close:this.props.CloseFunction}}
				cardClass = "global-filter-card"
			>
				{/*
					Dynamically create filters based on provided keys, similar to fields object in table.
					Each field can be given a type:
					"DropDown" -> Checks if the value of a field matches the selected option in the DropDown. Requires a list where the value of each option matches the desired value.
					"Date" -> Checks if the value of a field matches the date selected.
					"DateRange" -> Checks if the value of a field falls within a date range.
					"Greater Than" -> Checks if the value - must be a number - is greater than the selected number.
					"Less Than" -> Checks if the value - must be a number - is less than the selected number.
					"Equal To" -> Checks if the value - must be a number - is equal to the selected number.
					"Contains" -> Checks if the value contains the typed value. Must be a string.
					"Has" -> Checks if the length of the field is greater than zero. Must be an array.
				*/}
				<div className='filter-card-body'>
					{this.state.active&&this.GenerateForm(this.props.data)}
				</div>
				<div className='filter-card-footer action-row right'>
					<TextButton 
						text = "Clear"
						id = "filter-card-reset-button"
						ClickFunction = {this.ResetFilters}
					/>
					<TextButton 
						text = "Save"
						id = "filter-card-confirm-button"
						ClickFunction = {this.SetFilters}
						data = {this.props.data}
					/>
				</div>
			</Card>
        );
    }
}