const {Core} =require('../core/vapi-core.js');

class Mart extends Core{
    constructor({
        packs={},
        core={}
    }){
        super({...core});

        this.maps={};
        this.packs={}
        for(let p in packs){this.SETpack(p,packs[p],false)}
    }
    /**
     * 
     */
    Request({
        pack={},
        request='mart',
        shortpack=null
    }){
        return new Promise((resolve,reject)=>{
            if(this.packs[shortpack]){
                pack.collect = this.packs[shortpack].collect||'';
                pack.store = this.packs[shortpack].store || '';
                pack.db = this.packs[shortpack].db||'';
            }
            console.log('pack >',pack)
            this.SENDrequest({
                pack:pack,
                route:'STORE',
                request:request||''
            }).then(
                answr=>{
                    if(answr.success){
                        if(answr.body.success){
                            return resolve(answr.body.result);//returns the table if QUERY | INSERT
                        }else{return resolve(false);}
                    }else{return resolve(false);}
                }
            );
        });
    }

    SETpack(name,pack={},map=true){
        if(!pack.collect||pack.collect==''){return false;}
        if(!pack.store||pack.store==''){return false;}
        if(!pack.db||pack.db==''){return false;}
        this.packs[name]=pack;
        console.log(this.packs[name])
        if(map){
            this.Request({
                pack:{
                    ...this.packs[name],
                    method:'map'
                }
            }).then(
                answr=>console.log(answr)
            )
        }
        return true;
    }

    RUNmap(name,object){
        if(this.maps[name]){

            return true;
        }
        return false;
    }
}

module.exports={Mart}
