import React, { Component } from 'react';

/**
 * A title and descriptor of a paragraph.
 * @param {String} info.title | the title of the section.
 * @param {String} info.descr | the description of the section.
 */
export class SectionInfo extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
            <div className='section-info'>
                <div className='section-info-title'>{this.props.info.title}</div>
                <div className='section-info-descr'>{this.props.info.descr}</div>
            </div>
        );
    }
}